.section-cloud-service {
  display: block;
  width: 100%;
  position: relative;
  background: url("../Themes/itmetech/img/cloud-service-page/cloud_service_bg.jpg") no-repeat center;

  &:before {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    background: radial-gradient(circle at 60% 41%, rgba(0, 0, 0, 0), #000000);
  }

  .preview-cloud-service {
    display: block;
    width: 100%;
    position: relative;
    margin: 115px 0;
    padding-bottom: 85px;

    h2 {
      display: block;
      width: 100%;
      font-size: 38px;
      font-weight: 300;
      color: #ffffff;
      margin-bottom: 15px;
    }

    p {
      display: block;
      width: 100%;
      font-size: 18px;
      color: #ffffff;
      margin-bottom: 55px;
    }

    .preview-cloud-service-action {
      display: block;
      width: 100%;
      position: relative;

      .btn-viettel {
        margin-right: 15px;
        margin-bottom: 15px;
      }
    }
  }

  .tab-service {
    display: block;
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: rgba(16, 16, 16, 0.53);

    .tab-service-list {
      display: block;
      width: 100%;
      position: relative;
      text-align: left;
      @include media-breakpoint-down(xs) {
        text-align: center;
      }

      li {
        display: inline-block;

        a {
          display: inline-block;
          position: relative;
          font-size: 18px;
          font-weight: 500;
          color: #ffffff;
          padding: 15px 75px;
          background-color: transparent;
          @include transition(all .5s);
          @include media-breakpoint-down(xs) {
            padding: 15px;
          }

          &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 0;
            left: 0;
            top: 0;
            background-color: #25b2b3;
            @include transition(all .5s);
          }

          &.active, &:hover {
            background-color: #454747;

            &:after {
              height: 4px;
            }
          }
        }
      }
    }
  }
}

.cloud-service-content {
  display: block;
  width: 100%;
  position: relative;
  padding: 60px 0 0;
  background-color: white;

  .tab-review {
    display: block;
    width: 100%;
    position: relative;
    margin-bottom: 60px;

    .tab-review-content {
      display: block;
      width: 100%;
      font-size: 16px;
      color: #222222;
      margin: 15px 0;
    }

    .tab-review-image {
      display: inline-block;
      margin: 0;
      max-width: 100%;
    }
  }

  .feature-wrapper {
    display: block;
    width: 100%;
    position: relative;
    padding-left: 60px;
    margin-bottom: 30px;

    .feature-image {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: auto;
      max-width: 100%;
    }

    .feature-content {
      display: block;
      width: 100%;
      position: relative;

      h3 {
        display: block;
        width: 100%;
        position: relative;
        font-size: 18px;
        font-weight: 500;
        color: #424242;
        margin-bottom: 10px;
      }

      p {
        font-size: 16px;
        color: #222222;
        max-width: 400px;
      }
    }
  }

  .choose-wrapper {
    display: block;
    width: 100%;
    position: relative;
    padding-top: 60px;
    margin-bottom: 30px;

    .choose-image {
      position: absolute;
      left: 50%;
      top: 0;
      display: block;
      width: auto;
      max-width: 100%;
      @include translate(-50%, 0);
    }

    .choose-content {
      display: block;
      width: 100%;
      position: relative;

      h3 {
        display: block;
        width: 100%;
        position: relative;
        font-size: 18px;
        font-weight: 500;
        color: #424242;
        margin-bottom: 10px;
        text-align: center;
      }

      p {
        text-align: center;
        font-size: 16px;
        color: #222222;
        max-width: 265px;
        margin: 0 auto;
      }
    }
  }
}

.list-service-wrapper {
  display: block;
  width: 100%;
  position: relative;
  padding: 0 0 80px;
  background-color: white;

  .list-service {
    display: block;
    width: 100%;
    position: relative;
    margin: 10px 0;

    .list-service-title {
      display: block;
      width: 100%;
      opacity: 0.8;
      font-size: 36px;
      text-align: center;
      color: #222222;
      margin-bottom: 15px;
    }

    .list-service-type-tab {
      display: block;
      width: auto;
      position: absolute;
      margin-bottom: 0;
      right: 0;
      top: 0;
      @include media-breakpoint-down(xs) {
        position: relative;
        margin-bottom: 30px;
        text-align: right;
      }

      li {
        display: inline-block;
        margin: 0 0 0 5px;

        a {
          display: block;
          position: relative;
          font-size: 13px;
          font-weight: 500;
          text-align: center;
          color: #7f8287;
          width: 40px;
          height: 40px;
          padding: 8px 15px;
          background-color: transparent;
          text-transform: uppercase;
          @include border-radius(50%);
          @include transition(all .5s);

          i {
            position: absolute;
            left: 50%;
            top: 50%;
            font-size: 20px;
            @include translate(-50%, -50%);
          }

          &:hover, &.active {
            color: #fff;
            background-color: #25b2b3;
          }
        }
      }
    }

    .grid-list {
      display: block;
      width: 100%;
      position: relative;
      padding-top: 20px;

      .slick-prev, .slick-next {
        i {
          position: absolute;
          left: 0;
          top: 0;
          font-size: 50px;
          color: #F4F4F4;
          @include transition(all .5s);
          @include media-breakpoint-down(xs) {
            font-size: 35px;
          }
        }

        &:hover {
          i {
            color: #7F8287;
          }
        }

        &.slick-disabled {
          cursor: default;

          &:hover {
            i {
              color: #F4F4F4;
            }
          }
        }
      }

      .slick-prev {
        left: -50px;
        @include media-breakpoint-down(xs) {
          left: 10px;
          top: 0;
        }
      }

      .slick-next {
        @include media-breakpoint-down(xs) {
          right: 25px;
          top: 0;
        }
      }

      .item {
        display: block;
        width: 100%;
        position: relative;
        margin: 0 10px;

        .item-discount {
          display: block;
          position: absolute;
          width: 50px;
          height: 50px;
          right: -1px;
          top: -1px;
          background: url(../../../Themes/itmetech/img/svg/sale_orange.svg) right top no-repeat / 100% 100%;
          @include border-top-right-radius(4px);

          &.hot-value {
            background: url(../../../Themes/itmetech/img/svg/sale_red.svg) right top no-repeat / 100% 100%;
          }

          span {
            display: block;
            position: absolute;
            right: 5px;
            top: 5px;
            font-size: 11px;
            font-weight: 500;
            color: #ffffff;
          }
        }

        .item-price {
          display: block;
          width: 100%;
          position: relative;
          padding: 25px 0;
          text-align: center;
          background-color: #25b2b3;
          @include border-top-left-radius(4px);
          @include border-top-right-radius(4px);

          h4 {
            display: block;
            width: 100%;
            opacity: 0.8;
            font-size: 14px;
            font-weight: 500;
            text-align: center;
            color: #ffffff;
            text-transform: uppercase;
          }

          h3 {
            display: block;
            width: 100%;
            font-size: 36px;
            font-weight: 500;
            text-align: center;
            color: #ffffff;
          }

          span {
            display: block;
            width: 100%;
            font-size: 16px;
            text-align: center;
            color: #ffffff;
          }
        }

        .item-detail {
          display: block;
          width: 100%;
          position: relative;
          padding: 15px 0 0;
          text-align: center;
          border-right: solid 1px #e7e7e7;
          border-left: solid 1px #e7e7e7;

          p {
            font-size: 14px;
            text-align: center;
            color: #222222;

            &:last-child {
              margin: 0;
            }
          }
        }

        .item-action {
          display: block;
          width: 100%;
          position: relative;
          padding: 15px;
          text-align: center;
          border-right: solid 1px #e7e7e7;
          border-left: solid 1px #e7e7e7;
          border-bottom: solid 1px #e7e7e7;
          @include border-bottom-left-radius(4px);
          @include border-bottom-right-radius(4px);
        }
      }
    }

    .table-performance-double {
      width: 100%;
      position: relative;
      margin-top: 20px;

      thead {
        tr th {
          width: 20%;
          border: 1px solid #e7e7e7;
          border-bottom: 0;
          padding: 0;

          &.no-border {
            border: 0;
          }

          .table-head {
            display: block;
            position: relative;
            width: 100%;
            font-size: 13px;
            font-weight: 500;
            text-align: center;
            color: #555555;
            text-transform: uppercase;
            padding: 15px 20px;

            .item-discount {
              display: block;
              position: absolute;
              width: 45px;
              height: 45px;
              right: -1px;
              top: -1px;
              background: url(../../../Themes/itmetech/img/svg/sale_orange.svg) right top no-repeat / 100% 100%;
              @include border-top-right-radius(4px);

              &.hot-value {
                background: url(../../../Themes/itmetech/img/svg/sale_red.svg) right top no-repeat / 100% 100%;
              }

              span {
                display: block;
                position: absolute;
                right: 5px;
                top: 5px;
                font-size: 10px;
                font-weight: 500;
                color: #ffffff;
              }
            }
          }

          .table-price {
            display: block;
            width: 100%;
            font-weight: 500;
            text-align: center;
            padding: 15px 20px;
            background-color: #189b9b;
            font-size: 13px;
            color: #ffffff;

            h3 {
              font-size: 24px;
              text-align: center;
              color: #ffffff;
              display: inline-block;
              margin: 0;
              padding: 0;
            }
          }
        }
      }

      tbody {
        border: 1px solid #e7e7e7;

        tr {
          &:nth-child(odd) {
            background: rgba(24, 155, 155, 0.06);
          }

          td {
            border-left: 1px solid #e7e7e7;
            border-right: 1px solid #e7e7e7;
            padding: 0;

            .table-content {
              display: block;
              width: 100%;
              font-size: 16px;
              font-stretch: normal;
              color: #222222;
              padding: 15px 20px;
            }
          }
        }
      }
    }
  }
}

.related-products {
  display: block;
  width: 100%;
  position: relative;
  padding: 50px 0;
  background-color: #25b2b3;

  .related-products-title {
    display: block;
    width: 100%;
    position: relative;
    font-size: 36px;
    text-align: center;
    color: #ffffff;
    margin-bottom: 30px;
  }

  .related-products-wrapper {
    display: block;
    width: 100%;
    position: relative;
    text-align: center;
    margin-bottom: 15px;

    .related-products-block {
      display: block;
      width: 85px;
      height: 85px;
      position: relative;
      background-color: #ffffff;
      margin: 0 auto 15px;
      box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
      @include border-radius(50%);

      i {
        position: absolute;
        left: 50%;
        top: 50%;
        @include translate(-50%, -50%);

        &.icon-other-service {
          background: url("../Themes/itmetech/img/svg/other_service_icn.svg") no-repeat center;
          width: 40px;
          height: 45px;
        }

        &.icon-domain {
          background: url("../Themes/itmetech/img/svg/domain_color_icn.svg") no-repeat center;
          width: 41px;
          height: 41px;
        }

        &.icon-email-host {
          background: url("../Themes/itmetech/img/svg/email_host_color_icn.svg") no-repeat center;
          width: 44px;
          height: 43px;
        }

        &.icon-hosting {
          background: url("../Themes/itmetech/img/svg/hosting_color_icn.svg") no-repeat center;
          width: 44px;
          height: 44px;
        }
      }
    }

    .related-products-name {
      display: block;
      width: 100%;
      position: relative;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      color: #ffffff;
      margin-bottom: 10px;
    }
  }
}

.partner-list {
  display: block;
  width: 100%;
  position: relative;
  padding: 50px 0;
  background-color: #f5f5f6;

  .partner-list-title {
    display: block;
    width: 100%;
    position: relative;
    opacity: 0.8;
    font-size: 36px;
    text-align: center;
    color: #222222;
    margin-bottom: 25px;
  }

  .partner-list-wrapper {
    display: table;
    width: 100%;
    position: relative;
    @include media-breakpoint-down(xs) {
      display: block;
    }

    .item {
      display: table-cell;
      width: auto;
      position: relative;
      margin-bottom: 15px;
      text-align: center;
      @include media-breakpoint-down(xs) {
        display: block;
        width: 100%;
      }

      img {
        display: inline-block;
        max-width: 100%;
        margin: 0 auto;
      }
    }
  }
}

.btn-action {
  display: block;
  padding: 10px 0;
  font-size: 14px;
  font-weight: 500;
  text-align: center;
  line-height: 20px;
  text-decoration: none !important;
  position: relative;
  margin-bottom: 10px;
  @include border-radius(4px);
  @include transition(all .5s);

  &.transparent {
    color: #515151;
    border: solid 1px #d7d7d7;
    background-color: #ffffff;

    &:hover {
      background-color: #F2F2F2;
    }
  }

  &.orange {
    color: #ffffff;
    border: 2px solid #f78f24;
    background-color: #f78f24;

    &:hover {
      background-color: #E78224;
    }
  }

  &.green {
    color: #ffffff;
    border: 2px solid $green;
    background-color: $green;

    &:hover {
      background-color: darken($green, 10%);
    }
  }

  &.white {
    color: #3d3d3d;
    border: solid 1px #cccccc;
    background-color: #ffffff;
    font-weight: 500;
    min-height: 44px;

    &:hover {
      background-color: #F2F2F2;
    }
  }

  &.btn-md {
    min-width: 120px;
    min-height: 50px;
    line-height: 24px;
  }
}

.list-service-tab {
  display: block;
  width: 100%;
  position: relative;
  text-align: center;
  margin-bottom: 20px;

  li {
    display: inline-block;
    margin: 0 5px;

    a {
      display: block;
      font-size: 13px;
      font-weight: 500;
      text-align: center;
      color: #7f8287;
      width: 140px;
      padding: 8px 15px;
      border: solid 2px #d9d9d9;
      text-transform: uppercase;
      @include border-radius(20px);
      @include transition(all .5s);

      &:hover, &.active {
        color: #189b9b;
        border: solid 2px #25b2b3;
      }
    }
  }
}

.checkout-online-content {
  .checkout-card {
    max-width: 155px;
    margin: 20px auto 35px;
  }

  .custom-checkbox {
    max-width: 500px;
    margin: auto;
    margin-bottom: 10px;
  }
}
