.logo{
	img{
		max-width: 100%;
		@include media-breakpoint-down(xs){
			max-width: 120px;
		}
	}
}

.menu-left{
	display: inline-block;
	li{
		display: inline-block;
		margin-right: 20px;
		a{
			font-size: 15px;
		  color: #616161;
		  font-weight: 500;
		  &:hover{
		  	color: #333;
		  }
			&.highlight{
				border-radius: 3px;
	  		background-color: #25b2b3;
	  		color: #fff;
	  		padding: 9px 14px 13px;
			}
		}
		i.vt-icon{
			font-size: 32px;
		}
	}
}

.menu-right{
	text-align: right;
	li{
		display: inline-block;
		padding: 22px 10px;
		@include media-breakpoint-down(sm){
			padding: 25px 5px;
		}
	}
}

.menu-icon{
	@include transition(all .5s);
	&:hover{
		opacity: 0.6;
	}
}

.lang-switcher{
	background: none;
	border: none;
	outline: none!important;
	font-weight: 500;
}

.menudown{
	padding-right: 16px;
	position: relative;
	&:after{
		content: '';
		width: 12px;
		height: 7px;
		background: url(../../../Themes/itmetech/img/svg/arrow_down.svg) no-repeat center/cover;
		display: inline-block;
		position: absolute;
		right: 0;
		top: 50%;
		margin-top: -4px;
	}
}

@include media-breakpoint-down(sm){
	.top-nav{
		height: 76px;
	}
}