.wizard-nav{
	padding: 22px 0;
	background: #fff;
	border-top: 2px solid #dfdfdf;
}
.nav-tabs.wizard-tabs{
	text-align: center;
	border: none;
	display: block;
	.nav-item{
		display: inline-block;
		position: relative;
		&:not(:first-child){
			padding-left: 62px;
			&:before{
				content: '';
				display: inline-block;
				background: #dfdfdf;
				width: 56px;
				height: 3px;
				position: absolute;
				top: 50%;
				left: 0;
				margin-top: -2px;
			}
		}
	}
	.nav-link {
		border: none;
		font-size: 16px;
	  line-height: 1.88;
	  color: #353638;
		font-family: $font-cabin;
		padding: 0.5rem;
		.step-icon{
			display: inline-block;
			width: 30px;
			height: 30px;
			border-radius: 50%;
			text-align: center;
			line-height: 30px;
			font-size: 16px;
			color: #7f8287;
			font-family: $font-cabin;
			background: #f4f4f4;
			margin-right: 10px;
		}
		&.active, &.old{
			.step-icon{
				background: $turquoise;
				color: #fff;
			}
		}
	}
	@include media-breakpoint-down(sm){
		.nav-item{
			width: 100%;
			text-align: left;
			&:not(:first-child){
				padding-left: 0;
				&:before{
					display: none;
				}
			}
		}
	}
}

.card-password{
	border: none;
}

.card-package-selection{
	font-family: $font-cabin;  
	border: none;
	color: #353638;
	.card-title{
	  font-size: 18px;
	  font-weight: 500;
	  line-height: 1.67;
	  color: #353638;
	}
	.price{
		font-weight: 600;
		font-size: 16px;
	}
	.card-body{
		position: relative;
		z-index: 1;
		&.collapsable{
			cursor: pointer;
			.icon-arrow_more_icn{
				display: inline-block !important;
		    -webkit-transform: rotate(-90deg);
		    transform: rotate(-90deg);
		    position: absolute;
		    right: 25px;
		    top: 25px;
		    z-index: 0;
		    font-size: 13px;
		    color: #979797;
		    @include transition(all .5s ease);
			}
			&.collapsed{
				.icon-arrow_more_icn{
			    -webkit-transform: rotate(90deg);
			    transform: rotate(90deg);
			  }
			}
		}
	}
	.slider{
	}
}

.package-selection{
	color: rgba(53, 54, 56, 0.8);
	.bootstrap-select{
		width: 100%!important;
	}
	.package-info{
		padding-left: 36px;
		.package-des{
			font-family: $font-cabin;
		  font-size: 14px;
		  line-height: 1.29;
			color: rgba(53, 54, 56, 0.8);
			max-width: 430px;
		}
	}
}

.password-form{
	border: none;
	small{
		font-family: Cabin;
	  font-size: 14px;
	  color: rgba(53, 54, 56, 0.8);
	}
}

.card-cart{
	font-family: $font-cabin;
	border: none;
	color: #353638;
	.btn-action{
		font-family: $font-mont;
	}
	.card-title{
		color: rgba(53, 54, 56, 0.6);
		font-size: 18px;
		font-weight: 600;
	}
	.item-info{
		.name{
			font-size: 18px;
			font-weight: 600;
			&.small{
				font-size: 16px;
    		line-height: 1.88;
			}
		}
		.price{
			font-size: 18px;
			font-weight: 600;
			line-height: 1.88;
			&.small{
				font-size: 16px;
			}
		}
		.qtt,.duration{
			font-size: 14px;
			color: rgba(53, 54, 56, 0.8);
		}
	}
	.item-des{
		line-height: 1.29;
	  font-size: 14px;
	  color: rgba(53, 54, 56, 0.8);
		margin-bottom: 12px;
	}
	.price-total{
		border-top: 2px solid #313131;
		border-bottom: 2px solid #313131;
		padding: 15px 0;
		margin: 20px 0;
		font-size: 18px;
		font-weight: 600;
		.price{
			color: $turquoise;
			font-size: 20px;
		}
		.row:not(:last-child){
			margin-bottom: 10px;
		}
	}
}

.cart-section-title{
	font-family: $font-cabin;
	font-size: 16px;
  font-weight: bold;
  line-height: 1.88;
  color: rgba(53, 54, 56, 0.7);
  text-align: left;
  text-transform: uppercase;
  span{
  	text-transform: none;
  	font-size: 14px;
  	font-style: italic;
  	font-weight: 600;
  }
}

.related-products{
	&.layout-2{
		background: transparent;
		padding: 0;
		font-family: $font-cabin;
		.related-products-wrapper{
			background: #fff;
			border-radius: 4px;
			padding: 30px;    
			text-align: left;
			.related-products-block{
				width: auto;
				height: auto;
				box-shadow: none;
				margin: 0;
				background: transparent;
				position: absolute;
				border-radius: 0;
				i{
					display: block;
					top: 0px;
					left: 0px;
					webkit-transform: none;
    			transform: none;
				}
			}
			h3{
				padding-left: 60px;
				color: #353638;
				font-size: 18px;
				text-align: left;
				margin-bottom: 0;
			}
			.price{
				padding-left: 60px;
				font-size: 14px;
		  	color: rgba(53, 54, 56, 0.8);
			}
		}
	}
}

.cart-item{
	.item-name{
		font-size: 18px;
    font-weight: 600;
	}
	.item-des{
    line-height: 1.29;
    font-size: 14px;
    color: rgba(53, 54, 56, 0.8);
    margin-bottom: 0;
	}
	.row:not(:last-child){
		margin-bottom: 10px;
	}
}

.no-wrap{
	white-space: nowrap;
}

.table{
	&.cart-table{
		background: #fff;
		border-radius: 4px;
		font-family: $font-cabin;
		thead{
			tr{
				th{
					border: none;
  				color: rgba(53, 54, 56, 0.7);
  				font-size: 14px;
  				text-transform: uppercase;
				}
			}
		}
		tbody{
			tr{
				td{
					padding-top: 20px;
					padding-bottom: 20px;
				}
			}
		}
		tr{
			td{
				font-size: 16px;
				.item-des{
					max-width: 380px;
					min-width: 200px;
				}
			}
			@include media-breakpoint-up(xs){
				td, th{
					&:first-child, &:last-child{
						padding-left: 20px;
					}
				}
			}
		}
		a{
			color: #353638;
			&:hover{
				color: lighten(#353638, 30%);
			}
		}
		&.table-small{
			tr{
				td{
					.item-des{
						max-width: 300px;
					}
				}
			}
		}
	}
}

.cart-actions{
	.btn-action.orange{
		min-width: 160px;
		margin-left: 15px;
		@include media-breakpoint-up(sm){
			min-width: 330px;
		}
	}
	.btn-action.white{
		min-width: 160px;
	}
}

.coupon-form{
	font-size: 0;
	input{
		height: 48px;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
		border: 1px solid #cccccc;
		padding: 0 20px;
	  font-size: 16px;
	  @include calc(width, '100% - 110px');
    vertical-align: top;
	}
	.btn-submit{
		border-top-right-radius: 4px;
		border-bottom-right-radius: 4px;
		background-color: $turquoise;
		font-family: $font-mont;
	  font-size: 14px;
	  font-weight: 500;
	  line-height: 1;
	  text-align: center;
	  color: #ffffff;
	  height: 48px;
	  border: none;
	  padding: 0 19px;
    vertical-align: top;
	  &:hover{
	  	background-color: darken($turquoise, 10%);
			@include transition(all .5s);
	  }
	}
}

.cart-coupon{
	span{
		font-size: 14px;
		line-height: 1.57;
  	color: rgba(53, 54, 56, 0.7);
	}
}

.list-service-tab{
	&.checkout{
		.nav-link{
			width: 200px;
			font-weight: 600;
			font-family: $font-mont;
		}
	}
}

.account-info{
	border-radius: 4px;
  border: solid 2px #d9d9d9;
  padding: 30px;
  br{
  	content: "";
  	display: block;
    margin-bottom: 15px;
  }
}

.guide-info{
  padding: 30px 30px 30px 35px;  
  border-radius: 4px;
  background-color: rgba(53, 54, 56, 0.05);
  position: relative;
  p{
  	font-size: 14px;
	  font-style: italic;
	  line-height: 1.43;
	  color: #353638;
	  margin-bottom: 0;
  }
  &:before{
  	content: '';
  	width: 4px;
  	position: absolute;
  	height: 100%;
  	top: 0;
  	left: 0;
  	display: inline-block;
  	background-color: $orange;
  }
}

.checkout-success{
	margin: 75px auto;
	.card-cart{
		padding: 75px 25px; 
		color: #353638;
		.success-icon{
			width: 66px;
			margin-bottom: 15px;
		}
		h2{
			font-size: 28px;
		}
		p{
			color: rgba(53, 54, 56, 0.8);
		}
		.btn-action{
			margin: auto;
			width: 220px;
			font-weight: bold;
			font-family: $font-mont;
		}
	}
}