.custom-radio {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 5px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 17px;
  font-weight: 500;
  line-height: 1.5;
  color: #353638;
  font-family: $font-cabin;
  input {
	  position: absolute;
	  opacity: 0;
	  cursor: pointer;
	  height: 0;
	  width: 0;
	  &:checked{
	  	&+.radio-check{
	  		border-color: $turquoise;
	  		&:after{
	  			content: '';
	  			width: 13px;
	  			height: 13px;
	  			background: $turquoise;
	  			position: absolute;
	  			top: 4px;
	  			left: 4px;
	  			border-radius: 50%;
	  		}
	  	}
	  }
	}
	.radio-check {
	  position: absolute;
	  top: 0;
	  left: 0;
	  height: 25px;
	  width: 25px;
	  border: solid 2px #353638;
	  border-radius: 50%;
	  display: inline-block;
	}
}

.bootstrap-select{
	margin-bottom: 12px;
	&>.dropdown-toggle{
		border-radius: 4px;
	  border: solid 1px #b5b5b5;
	  background-color: #ffffff;
	  .filter-option-inner-inner{
		  font-family: $font-cabin;
		  font-size: 16px;
		  line-height: 1.56;
		  color: rgba(53, 54, 56, 0.8);
	  }
	}
	.dropdown-item.active, .dropdown-item:active{
		background-color: $turquoise;
	}
	.dropdown-menu li a span.text{
		font-family: $font-cabin;
		font-size: 16px;
		color: rgba(53, 54, 56, 0.8);
	}
}

.custom-checkbox {
  display: block;
  position: relative;
  padding-left: 30px;
  margin-bottom: 5px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  color: rgba(53, 54, 56, 0.8);
  font-family: $font-cabin;
  text-align: left;
  input {
	  position: absolute;
	  opacity: 0;
	  cursor: pointer;
	  height: 0;
	  width: 0;
	  &:checked{
	  	&+.checkbox-check{
	  		color: $turquoise;
	  		border-color: $turquoise;
	  		.icon-check-mark_icn{
	  			display: block;
	  			position: relative;
	  			top: -1px;
	  			left: 1px;
	  		}
	  	}
	  }
	}
	.checkbox-check {
	  position: absolute;
	  top: 0;
	  left: 0;
	  height: 18px;
	  width: 18px;
	  border: solid 1px #b5b5b5;
	  border-radius: 4px;
	  display: inline-block;
	  .icon-check-mark_icn{
	  	display: none;
	  }
	}
}

.bootstrap-select{
	margin-bottom: 12px;
	&>.dropdown-toggle{
		border-radius: 4px;
	  border: solid 1px #b5b5b5;
	  background-color: #ffffff;
	  .filter-option-inner-inner{
		  font-family: $font-cabin;
		  font-size: 16px;
		  line-height: 1.56;
		  color: rgba(53, 54, 56, 0.8);
	  }
	}
	.dropdown-item.active, .dropdown-item:active{
		background-color: $turquoise;
	}
	.dropdown-menu li a span.text{
		font-family: $font-cabin;
		font-size: 16px;
		color: rgba(53, 54, 56, 0.8);
	}
}