.section-title{
  opacity: 0.8;
  font-family: $font-mont;
  font-size: 36px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.61;
  letter-spacing: normal;
  text-align: center;
  &.white{
  	color: #fff;
  	opacity: 1;
  }
}

.section-subtitle{
	opacity: 0.8;
  font-family: $font-cabin;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: #222222;
  font-weight: 500;
}

.section-services{
	background-color: $turquoise;
	padding: 33px 0;
	position: relative;
	&:before{
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		background-color: rgba(0, 0, 0, 0.24);
	}
	.service-icon{
		position: relative;
		z-index: 2;
		text-align: center;
		a{
			text-decoration: none;
			span{
				display: block;
				margin: auto;
				margin-top: 15px;
				text-align: center;
			  font-family: $font-cabin;
			  font-size: 16px;
			  font-weight: 500;
			  font-style: normal;
			  font-stretch: normal;
			  line-height: normal;
			  letter-spacing: normal;
			  color: #ffffff;
			}
		}
	}
	@include media-breakpoint-up(sm){
		.row{
			.col-sm-4:not(:nth-child(3n)){
				.service-icon{
					border-right: 2px solid #54a4a5;
				}
			}
		}
	}
	@include media-breakpoint-down(xs){
		.row{
			.col-sm-4:not(:last-child){
				.service-icon{
					border-bottom: 2px solid #54a4a5;
					margin-bottom: 20px;
					padding-bottom: 20px;
				}
			}
		}
	}
}

.section-solutions{
	background-color: $bg-gray;
	padding: 70px 0 80px;
	@include media-breakpoint-down(xs){
		.container{
			padding: 0;
		}
		.tab-content{
			margin-top: 0!important;
		}
	}
}

.section-why{
	padding: 75px 0 85px;
	color: #fff;
	background: url(../../../Themes/itmetech/img/why-bg.jpg) no-repeat center/cover;
	.why-icon{
		text-align: center;
		h5{
			opacity: 0.9;
		  font-size: 20px;
		  font-weight: 500;
		  font-style: normal;
		  font-stretch: normal;
		  line-height: 1.5;
		  letter-spacing: normal;
		  margin-top: 15px;
		}
		p{
			font-size: 16px;
		  font-weight: normal;
		  font-style: normal;
		  font-stretch: normal;
		  line-height: 1.56;
		  letter-spacing: normal;
		  font-family: $font-notosans;
		  max-width: 300px;
		  margin: auto;
		}
		@include media-breakpoint-down(xs){
			margin-bottom: 50px;
		}
	}
	@include media-breakpoint-down(xs){
		background: #2d2d2d;
	}
}

.section-stat{
	padding: 60px 0;
	background: #fff;
	.stat-item{
		text-align: center;
		.num{
			font-size: 46px;
		  font-weight: 500;
		  font-style: normal;
		  font-stretch: normal;
		  line-height: normal;
		  letter-spacing: normal;
		  text-align: center;
		  color: $orange;
		}
		.text{
			opacity: 0.6;
		  font-size: 16px;
		  font-weight: 500;
		  font-style: normal;
		  font-stretch: normal;
		  line-height: 1.56;
		  letter-spacing: normal;
		  text-align: center;
		  color: #222222;
		  max-width: 220px;
		  margin: auto;
		}
	}
}

.section-customers{
	background-color: #2d2d2d;
	background-image: url(/Themes/itmetech/img/sphere-ptrn@2x.png);
	background-repeat: no-repeat;
	background-position: top 25px right;
	background-size: auto 100%;
	padding: 75px 0 100px;
	color: #fff;
	.section-title{
		font-size: 32px;
		text-align: left;
	  font-family: $font-cabin;
	}
	.section-content{
		font-size: 16px;
	  font-family: $font-opensans;
	  line-height: 1.63;
	  max-width: 400px;
	  text-align: justify;
	}
	.view-more{
		color: #fff;
		text-decoration: none;
  	@include transition(all .5s);
		&:hover{
			color: darken(#fff, 30%);
		}
		i{
			font-size: 12px;
			margin-left: 15px;
		}
	}
	@include media-breakpoint-down(xs){
		text-align: center;
		.section-title{
			text-align: center;
		}
		.section-content{
			margin: auto;
		}
		.view-more{
			margin-bottom: 30px;
			display: inline-block;
		}
	}
	.partners{
		text-align: center;
		.partner-img{
			display: inline-block;
			margin: 0 20px 25px;
			vertical-align: top;
		}
	}
	@include media-breakpoint-down(xs){
		background-image: none;
	}
}

.section-trial{
	background-color: $turquoise;
	padding: 65px 0 75px;
	color: #fff;
	p{
		 font-size: 28px;
	  font-weight: normal;
	  line-height: 1.36;
	  letter-spacing: -0.5px;
	}
}

.section-subscribe{
	padding: 35px 0;
	background: #fff;
	.social-links{
		margin-top: 12px;
		&>span{
			font-family: $font-cabin;
		  font-size: 18px;
		  color: $grey;
		  margin-right: 20px;
		}
		.social-link{
			margin-right: 6px;
			text-decoration: none!important;
			&:hover{
				opacity: 0.8;
				@include transition(all .5s);
			}
		}
	}
	.subscribe-form{
		font-size: 0;
		label{
			font-family: $font-notosans;
		  font-size: 18px;
		  color: $grey;
		  margin-right: 20px;
		}
		#your-email{
			height: 60px;
			border-top-left-radius: 4px;
			border-bottom-left-radius: 4px;
  		background-color: #efefef;
  		border: none;
  		padding: 0 20px 0 50px;
		  font-size: 16px;
		  background: url(../../../Themes/itmetech/img/svg/plane_icn.svg) no-repeat top 20px left 20px #efefef;
		  @include calc(max-width, '100% - 240px');
		}
		.btn-submit{
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
			background-color: $orange;
			font-family: $font-mont;
		  font-size: 16px;
		  font-weight: 500;
		  line-height: 1;
		  text-align: center;
		  color: #ffffff;
		  height: 60px;
		  border: none;
		  padding: 0 38px;
		  &:hover{
		  	background-color: darken($orange, 10%);
				@include transition(all .5s);
		  }
		}
	}
	@include media-breakpoint-down(sm){
		.social-links{
			margin-bottom: 30px;
			text-align: center;
			&>span{
				display: block;
				text-align: center;
				margin-bottom: 10px;
				margin-right: 0!important;
			}
			.social-link img{
				width: 45px;
				height: 45px;
			}
		}
		.subscribe-form{
			label{
				display: block;
			}
			#your-email{
				width: 100%;
				max-width: 100%;
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px;
			}
			.btn-submit{
				display: block;
				width: 100%;
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
				margin-top: 10px;
			}
		}
	}
}

.section-our-services{
	padding: 65px 0 30px;
	background: #fff;
}

.banner-slider{
	.carousel-item{
		min-height: 500px;
	}
}

.slide-solid{
	padding: 65px 0;
	&.turquoise{
		background-color: $turquoise;
		.slider-text{
			h3{
				font-size: 38px;
			  font-weight: 300;
			  line-height: 1.11;
			  color: #ffffff;
			}
			p{
				font-family: $font-cabin;
			  font-size: 18px;
			  line-height: 1.44;
			  color: #ffffff;
			}
		}
	}
	.btn-transparent{
		border-radius: 4px;
 	  border: solid 2px #ffffff;
 	  font-size: 14px;
	  font-weight: 500;
	  line-height: 1.14;
	  letter-spacing: 0.5px;
	  color: #ffffff;
	  padding: 18px 27px;
	}
}

.float-buttons{
	position: fixed;
	right: 25px;
	bottom: 25px;
	z-index: 40;
	.float-button{
		border: none;
		width: 52px;
	  height: 52px;
	  display: block;
	  position: relative;
	  outline: 0!important;
	  &.top{
	  	background: url(../../../Themes/itmetech/img/svg/top-btn.svg) no-repeat center/cover;
	  }
	  &.call{
	  	background: url(../../../Themes/itmetech/img/svg/call-btn.svg) no-repeat center/cover;
	  }
	  &.messenger{
	  	background: url(../../../Themes/itmetech/img/svg/messenger-btn.svg) no-repeat center/cover;
	  }
	  &.support{
	  	background: url(../../../Themes/itmetech/img/svg/support-btn.svg) no-repeat center/cover;
	  }
	  &:after{
	  	content: '';
	  	width: 42px;
	  	height: 42px;
	  	position: absolute;
	  	top: 3px;
	  	left: 5px;
	  	display: inline-block;
	  	background: rgba(0,0,0,0.2);
	  	opacity: 0;
	  	visibility: hidden;
	  	border-radius: 50%;
	  	@include transition(all .5s ease);
	  }
	  &:hover{
	  	&:after{
	  		opacity: 1;
	  		visibility: visible;
	  	}
	  }
	}
}