.footer{
	padding-top: 65px;
	background-color: #2d2d2d;
	color: darken(#fff, 20%);
	font-size: 16px;
	font-family: $font-cabin;
  line-height: 1.56;
	.footer-info{
		margin-bottom: 30px;
		color: darken(#fff, 40%);
		a{
			color: darken(#fff, 40%);
			&:hover{
				color: darken(#fff, 20%);
			}
		}
	}
	.footer-block{
		margin-bottom: 30px;
		&>h5{
			font-family: $font-mont;
			color: darken(#fff, 40%);
			font-size: 12.5px;
		  font-weight: 600;
		  line-height: 1.46;
		  letter-spacing: 0.5px;
		  text-transform: uppercase;
		}
	}
	.footer-list{
		padding: 0;
		margin: 0;
		list-style: none;
		a{
			color: darken(#fff, 20%);
			font-family: $font-mont;
		  font-size: 15px;
		  line-height: 1.67;
		  letter-spacing: normal;
		  margin-bottom: 6px;
		  display: inline-block;
		  &:hover{
		  	color: #fff;
		  }
		}
	}
	.certificates{
		img{
			margin-bottom: 35px;
			margin-right: 20px;
		}
	}
	@include media-breakpoint-down(xs){
		.footer-info, .footer-block, .certificates{
			padding-left: 20px;
			padding-right: 20px;
		}
		.footer-block > h5{
			font-size: 17px;
			letter-spacing: -0.2px;
		}
		.certificates{
			img{
				margin-right: 7px;
			}
		}
	}
}

.lang-switcher-mobile{
	padding-left: 20px;
	padding-right: 20px;
	color: #fff;
	font-size: 20px;
	position: relative;
	width: 100%;
	display: block;
	margin-bottom: 10px;
	&:hover{
		color: #cccccc;
	}
	i{
		display: inline-block;
		-webkit-transform: rotate(90deg);
    transform: rotate(90deg);
    position: absolute;
    right: 25px;
    font-size: 16px;
    color: #999999;
    top: 5px;
	}
	&+.nav{
		li{
			width: 100%;
			a{
				color: #fff;
				font-size: 16px;
				padding-left: 52px;
				&:hover{
					color: #999999;
				}
			}
		}
	}
}

.footer-bottom{
	border-top: 1px solid #3d3d3d;
	padding: 30px 0;
	.copyright-text{
		font-family: $font-notosans;
	  font-size: 13px;
	  color: #b2b2b2;
	}
	.footer-menu{
		text-align: center;
		li{
			display: inline-block;
			a{
				opacity: 0.8;
			  font-family: $font-notosans;
			  font-size: 14px;
			  line-height: 1.79;
			  color: #b2b2b2;
			  padding: 0 22px;
			  &:hover{
			  	opacity: 1;
			  }
			}
		}
	}

.lang-switcher{
	color: #b2b2b2;
	img{
		width: 18px;
	}
	&.menudown:after{
		background: url(../../../Themes/itmetech/img/svg/arrow_down_grey.svg) no-repeat center/cover;
	}
}
	@include media-breakpoint-down(md){
		.copyright-text{
			display: block;
			margin-bottom: 20px;
			text-align: center;
		}
	}
}