/* Base Styles -------------------- */
body{
	color: $body-font-color;
	font-family: $font-mont;
	width:100%;
	overflow-x: hidden;
	background: #f5f5f6;
}

ul{
	list-style: none;
	padding: 0;
	margin: 0;
}

a{
	@include transition(all .5s);
	&:hover{
		text-decoration: none;
	}
}