@font-face {
	font-family: "VTIcons";
	src: url('../fonts/VTIcons.eot');
	src: url('../fonts/VTIcons.eot?#iefix') format('eot'),
		url('../fonts/VTIcons.woff2') format('woff2'),
		url('../fonts/VTIcons.woff') format('woff'),
		url('../fonts/VTIcons.ttf') format('truetype'),
		url('../fonts/VTIcons.svg#VTIcons') format('svg');
}

.vt-icon:before {
	font-family: "VTIcons";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal;
	/* speak: none; only necessary if not using the private unicode range (firstGlyph option) */
	text-decoration: none;
	text-transform: none;
}


