.col-sm-45{
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}
@media (min-width: 576px){
	.col-sm-45 {
    flex: 0 0 37.5%;
    max-width: 37.5%;
	}
}

.row{
	&.row-sm{
		margin-left: -5px;
		margin-right: -5px;
		[class^='col-']{
			padding-left: 5px;
			padding-right: 5px;
		}
	}
}
