@import 'buttons';
@import 'nav';
@import 'carousel';
@import 'expandable';
@import 'bootstrap-select';
@import 'bootstrap-slider';
@import 'checkbox';
@import 'form';

.nav-tabs.vt-tabs{
	border-bottom: none;
	text-align: center;
	font-size: 0;
	li{
		display: inline-block;
		border-bottom: 2px solid $border-color;
		padding: 0 30px;
		a{
			font-weight: 600;
			line-height: 30px;
			font-size: 15px;
			color: #a6acb3;
			text-transform: uppercase;
			position: relative;
			border: none;
			padding: 0.3rem 0.5rem;
			&:hover, &.active{
				background-color: transparent;
				border: none;
				color: #4c5056;
				&:after{
					content: '';
					display: inline-block;
					width: 100%;
					height: 3px;
					background-color: $orange;
					position: absolute;
					bottom: -1px;
					left: 0;
				}
			}
		}
		@include media-breakpoint-down(sm){
			padding: 0 5px;
		}
		@include media-breakpoint-down(xs){
			width: 100%;
			padding: 0;
			background: #fff;
			a{
				text-align: left;
				padding-left: 25px;
				padding-top: 20px;
				padding-bottom: 20px;
				position: relative;
				&:hover, &.active{
					&:after{
						content: '';
						width: 3px;
						height: 100%;
						background: $orange;
						display: inline-block;
						position: absolute;
						top: 0;
						left: 0;
					}
				}
			}
		}
	}
	@include media-breakpoint-up(sm){
		display: inherit;
	}
	@include media-breakpoint-down(xs){
	}
}

.tab-switcher{
	font-size: 16px;
	background-color: #fff;
	padding: 22px 25px;
	border-bottom: 1px solid #ebebec;
	position: relative;
	font-weight: 500;
	cursor: pointer;
	color: #4c5056!important;
	&:after{
		content: '';
		width: 3px;
		height: 100%;
		background: $orange;
		display: inline-block;
		position: absolute;
		top: 0;
		left: 0;
	}
	.vt-icon{
		font-size: 16px;
		line-height: 32px;
		position: absolute;
		top: 50%;
		margin-top: -16px;
		right: 25px;
		color: #bfbfbf;
		display: block;
		height: 32px;
		@include transition(all .5s ease);
	}
	&:not(.collapsed){
		.vt-icon{
			-webkit-transform: rotate(90deg);
    	transform: rotate(90deg);
			margin-top: -15px;
		}
	}
}

.card.card-solution{
	border-color: $border-color;
	padding: 40px 35px;
	.card-title{
	  font-size: 32px;
	  font-weight: normal;
	  font-style: normal;
	  font-stretch: normal;
	  line-height: normal;
	  letter-spacing: normal;
	  color: #373737;
	  font-weight: 500;
	}
	.card-text{
		opacity: 0.8;
	  font-family: $font-opensans;
	  font-size: 16px;
	  font-weight: normal;
	  font-style: normal;
	  font-stretch: normal;
	  line-height: 1.5;
	  letter-spacing: normal;
	  color: #222222;
	  text-align: justify;
	}
}

ul.checked-list{
	list-style: none;
	padding: 0;
	margin: 0;
	li{
		padding-left: 32px;
		position: relative;
		float: left;
		width: 100%;
		font-size: 14px;
	  font-weight: 600;
	  font-style: normal;
	  font-stretch: normal;
	  line-height: normal;
	  letter-spacing: 0.3px;
	  color: #515151;
	  line-height: 22px;
	  margin-bottom: 10px;
		&:before{
			content: '';
			width: 21px;
			height: 21px;
			display: inline-block;
			background: url(../../../Themes/itmetech/img/svg/check_orange_icn.svg);
			background-size: cover;
			position: absolute;
			left: 0;
		}
		@include media-breakpoint-up(sm){
			width: 50%;
		}
		@include media-breakpoint-up(md){
			width: 25%;
		}
	}
}