.btn-transparent{
	border: 2px solid #fff;
	padding: 17px 44px;
	font-size: 16px;
  font-weight: 500;
  line-height: 1;
  text-align: center;
  color: #ffffff;
  line-height: 20px;
  text-decoration: none!important;
  position: relative;
  display: inline-block;
  @include transition(all .5s);
  &.turquoise{
  	&:hover{
  		color: $turquoise;
  		background-color: #fff;
  	}
  }
  i.vt-icon{
  	font-size: 12px;
  	position: absolute;
  	right: 15px;
  	top: 50%;
  	margin-top: -11px;
  }
}

.badge{
  font-style: normal;
  border-radius: 10px;
  font-weight: normal;
  font-size: 10px;
  padding: 0.5em 0.8em;
  vertical-align: middle;
  &.badge-rounded{
    border-radius: 50%;
  }
  &.badge-orange{
    border: 1.5px solid #fff;
    background-color: #f78f24;
    color: #fff;
    text-align: center;
  }
  &.badge-warning{
    background-color: $orange;
    color: #fff;
  }
  &.badge-success{
    background-color: $turquoise;
    color: #fff;
  }
}

.btn-viettel{
  padding: 17px 44px;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  line-height: 20px;
  text-decoration: none!important;
  position: relative;
  display: inline-block;
  @include border-radius(4px);
  @include transition(all .5s);
  @include media-breakpoint-down(xs) {
    display: block;
    width: 100%;
  }
  &.transparent{
    border: 2px solid #fff;
    background-color: transparent;
    &:hover{
      color: $turquoise;
      border: 2px solid #fff;
      background-color: #fff;
    }
  }
  &.orange{
    border: 2px solid #f78f24;
    background-color: #f78f24;
    &:hover{
      color: $orange;
      border: 2px solid #fff;
      background-color: #fff;
    }
  }
}
